<template>
  <div class="topLeft list_box">
    <div id="topLeft"></div>
  </div>
</template>

<script>
export default {
  name: "lineChart",
  data() {
    return {
      option: {},
    };
  },
  methods: {
    createData() {
      var chartDom = document.getElementById("topLeft");
      var myChart = this.$echarts.init(chartDom);
      var option;

      const colors = ["#5470C6", "#91CC75", "#EE6666"];
      option = {
        color: colors,
        title: {
          text: "园区电水气使用统计",
          left: "center",
          textStyle: {
            color: "#fff",
            fontWeight: "normal",
          },
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
          },
        },
        grid: {
          top: "15%",
          left: "8%",
          right: "20%",
          bottom: "19%",
        },

        legend: {
          data: ["用电", "用水", "用气"],
          bottom:15,
          textStyle:{
            color:'#fff'
          }
        },
        xAxis: [
          {
            type: "category",
            axisTick: {
              alignWithLabel: true,
            },
            axisLabel: {
            //坐标轴上文字颜色
            interval: 0,
            color: "#fff",
          },
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
          },
        ],
        yAxis: [
        
          {
            type: "value",
            name: "用水(t)",
            min: 0,
            max: 250,
            position: "right",
            splitLine: {
              show: false, //去掉网格线
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: colors[0],
              },
            },
          },
          {
            type: "value",
            name: "用气(m³)",
            min: 0,
            max: 250,
            position: "right",
            offset: 60,
            splitLine: {
              show: false, //去掉网格线
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: colors[1],
              },
            },
          },
            {
            type: "value",
            name: "用电(kWh)",
            min: 0,
            max: 25,
            position: "left",
            axisLine: {
              show: true,
              lineStyle: {
                color: colors[2],
              },
            },
            splitLine: {
              show: false, //去掉网格线
            },
          },
        ],
        series: [
          {
            name: "用水",
            type: "bar",
            data: [
              2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4,
              3.3,
            ],
          },
          {
            name: "用气",
            type: "bar",
            yAxisIndex: 1,
            data: [
              2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0,
              2.3,
            ],
          },
          {
            name: "用电",
            type: "bar",
            yAxisIndex: 2,
            data: [
              2.0, 2.2, 3.3, 4.5, 6.3, 10.2, 20.3, 23.4, 23.0, 16.5, 12.0, 6.2,
            ],
          },
        ],
      };

      option && myChart.setOption(option);
    },
  },
  mounted() {
    this.createData();
  },
};
</script>

<style lang="less" scoped>
#topLeft {
  width: 100%;
  height: 100%;
}
.topLeft {
  width: 33%;
  padding-top: 20px;
  margin-right: 10px;
  box-sizing: border-box;
}
</style>
